html {
  -webkit-text-size-adjust: 100%;
  -webkit-font-feature-settings: normal;
  font-feature-settings: normal;
  font-family: ui-sans-serif, system-ui, -apple-system, Quicksand, BlinkMacSystemFont,
    Segoe UI, Roboto, Helvetica Neue, Arial, Noto Sans, sans-serif,
    Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji;
  line-height: 1.5;
  tab-size: 4;
}

body {
  height: 100vh;
  height: fill-available;
  height: -webkit-fill-available;
}

*,
:after,
:before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.App {
  font-family: Quicksand, "Helvetica Neue", sans-serif;
  transition-duration: 0.5s;
}

[data-mobile="true"] {
  height: 100%;
}

svg {
  display: block;
  vertical-align: middle;
}

.hidden {
  display: none;
}

.scale-0 {
  --tw-scale-x: 0;
  --tw-scale-y: 0;
  -webkit-transform: translate(var(--tw-translate-x), var(--tw-translate-y)) rotate(var(--tw-rotate)) skewX(var(--tw-skew-x)) skewY(var(--tw-skew-y)) scaleX(var(--tw-scale-x)) scaleY(var(--tw-scale-y));
  transform: translate(var(--tw-translate-x), var(--tw-translate-y)) rotate(var(--tw-rotate)) skewX(var(--tw-skew-x)) skewY(var(--tw-skew-y)) scaleX(var(--tw-scale-x)) scaleY(var(--tw-scale-y));
}

::-webkit-scrollbar {
  background-color: initial;
  width: 0.25rem;
}

::-webkit-scrollbar-thumb {
  --tw-bg-opacity: 1;
  background-color: rgb(53 55 64 / var(--tw-bg-opacity));
  border-radius: 0.75rem;
}

body.dark [class*="chat-view"] {
  background-color: rgb(53 55 64/1);
}

body.dark [class*="markdown"],
body.dark [class*="createdAt"] {
  color: rgb(226 232 240/1) !important;
}

body.dark [class*="form"] [class*="dropdown"] {
  border-color: rgb(32 33 35 / 1) !important;
  color: rgb(255 255 255 / 1) !important;
  background-color: inherit;
}

body.dark [class*="form"] [class*="dropdown"] option {
  background-color: rgb(32, 33, 35);
}

body.dark [class*="form"] textarea {
  background-color: rgb(53 55 64 / 1) !important;
  border-color: rgb(32 33 35 / 1) !important;
  color: rgb(255 255 255 / 1) !important;
}

body.dark [class*="btn-send"] {
  border-color: rgb(32 33 35 / 1) !important;
  color: rgb(255 255 255 / 1) !important;
  background-color: inherit !important;
}