.sidebar {
  background-color: rgb(32 33 35/1);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-shrink: 0;
  width: 29%;
  height: 100%;
  padding: 2rem 1.25rem 1.25rem;
  position: relative;
  row-gap: 1rem;
  transition-duration: .5s;
  color: #fff;

  .title-logo {
    align-items: center;
    display: flex;
    justify-content: space-around;

    .logo {
      width: 2rem;
      height: 2rem;

      img {
        max-width: 100%;
        height: auto;
      }
    }

    .title {
      font-size: 1.5rem;
      font-weight: 500;
      line-height: 1.75rem;
    }

    .close-btn {
      cursor: pointer;

      .btn-icon {
        height: 2rem;
        width: 2rem;
      }
    }
  }

  .bottom {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;

    .item {
      align-items: center;
      border-radius: .375rem;
      color: rgb(255 255 255/1);
      -webkit-column-gap: 1rem;
      column-gap: 1rem;
      cursor: pointer;
      display: flex;
      font-size: 1rem;
      height: 3rem;
      line-height: 1.5rem;
      padding: 1rem;
      width: 100%;

      &:hover {
        background-color: #ffffff2b;
      }

      .icon {
        cursor: pointer;
        font-size: 1.25rem;
        line-height: 1.75rem;
        transition-duration: .5s;
      }

      span {}
    }
  }

  &.narrow {
    width: 5rem;

    .bottom {
      .item {
        padding: 0;
        justify-content: center;
      }
    }
  }

  &.isMobile {
    width: 3rem;
    padding: 2rem .5rem 1.25rem;

    .bottom {
      .item {
        padding: 0;
        justify-content: center;
      }
    }
  }
}

@media screen and (min-width: 1038px) {
  .sidebar {
    width: 18rem;
  }
}