.message {
  align-items: flex-start;
  display: flex;
  gap: .5rem;
  justify-content: space-between;
  margin: .3rem;
  padding: 1rem;

  .wrapper {
    flex: 1 1;
    flex-wrap: wrap;
    overflow: hidden;
    width: 100vw;

    .text-left {
      text-align: left;
    }

    .text-right {
      text-align: right;
    }

    .markdown {
      --tw-text-opacity: 1;
      color: rgb(51 65 85/var(--tw-text-opacity));
      font-size: 1.25rem;
      line-height: 1.75rem;

      pre {
        font-size: 1rem;
      }

      &.blink p {
        animation: blink-2 1.3s both infinite;

        @-webkit-keyframes blink-2 {
          0% {
            opacity: 1
          }

          50% {
            opacity: .2
          }

          100% {
            opacity: 1
          }
        }

        @keyframes blink-2 {
          0% {
            opacity: 1
          }

          50% {
            opacity: .2
          }

          100% {
            opacity: 1
          }
        }
      }

    }

    .createdAt {
      --tw-text-opacity: 1;
      color: rgb(71 85 105/var(--tw-text-opacity));
      font-size: .875rem;
      font-style: italic;
      font-weight: 100;
      line-height: 1.25rem;
    }

    &.image {
      display: flex;
      justify-content: space-between;

      >div {
        width: 49%;
        position: relative;
      }

      .img {
        max-width: 100%;
        height: auto;
        border-radius: 0.5rem;
        vertical-align: top;
      }
    }
  }

  .pic {
    --tw-bg-opacity: 1;
    align-items: center;
    background-color: rgb(226 232 240/var(--tw-bg-opacity));
    border-radius: 9999px;
    display: flex;
    font-size: 1.25rem;
    height: 2.5rem;
    justify-content: center;
    line-height: 1.75rem;
    margin-left: .8rem;
    width: 2.5rem;

    .bot {
      max-width: 75%;
      height: auto;
    }

    .rounded-full {
      width: 2.5rem;
      height: 2.5rem;
      border-radius: 9999px;
    }
  }

  &.flex-row-reverse {
    flex-direction: row-reverse;

    .pic {
      margin-left: 0;
      margin-right: .8rem;
    }
  }

  &.isMobile {
    padding: .5rem;

    .wrapper {
      .markdown {
        font-size: 1rem;
        line-height: 1.2rem;
      }

      &.image {}
    }

    .pic {
      margin-left: 0.4rem;
      width: 2rem;
      height: 2rem;
    }

    .rounded-full {
      width: 2rem;
      height: 2rem;
    }

    &.flex-row-reverse {
      .pic {
        margin-left: 0;
        margin-right: 0.4rem;
      }
    }
  }
}

.modal-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 100;

  .close-btn {
    position: fixed;
    top: 1%;
    right: 1%;
    font-size: 3rem;
    color: #fff;
    cursor: pointer;

    .close-icon {}
  }

  .image-wrapper {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;

    img {
      width: 60%;
    }
  }
}