.wrapper {
  display: flex;
  align-items: center;
  height: 100vh;
  overflow: hidden;

  .container {
    width: 100%;
    font-weight: 600;
    text-align: center;

    &.explain1 {
      .logo-wrapper {
        display: flex;
        align-items: center;
        justify-content: center;
        color: #606060;
        margin-bottom: 1vh;

        img {
          margin-right: .3rem;
        }

        h1 {
          font-size: 3rem;
        }
      }

      .image-wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 5vh;

        figure {
          text-align: center;

          figcaption {}

          img {
            width: auto;
            height: 38vh;
          }
        }


        p {
          font-weight: 600;
          font-size: 1.6rem;
          word-break: keep-all;
        }
      }

      .image-wrapper2 {
        figcaption {
          font-size: 1.7rem;
          margin-bottom: 2vh;

          span {
            color: tomato;
          }
        }

        img {
          height: 9vh;
        }
      }
    }

    &.explain2 {}

    &.explain3 {}
  }

  &.isMobile {
    .container {
      &.explain1 {
        h1 {
          font-size: 2rem;
        }
      }

    }
  }
}

.modal-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 100;

  .close-btn {
    position: fixed;
    top: 1%;
    right: 1%;
    font-size: 3rem;
    color: #fff;
    cursor: pointer;

    .close-icon {}

    .text-wrapper {}
  }
}